@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,400i,600,700,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth;
}
body{
  font-family: 'Open Sans', sans-serif;
}
btn:focus{
  box-shadow: none!important;
}
.withscroll{
  overflow-y: scroll;
}
.noscroll{
  overflow-y:hidden;
}
.kepala{
  min-height: 85vh;
}

.kepala h3{
  font-weight: bold;
  font-size: 60px;
  margin-bottom: 0px;
}
.kepala h1{
  font-weight: bold;
  font-size: 90px;
  color: #1E60A2;
  left: -3px;
  position: relative;
  top: -20px;
  margin-bottom: 0px
}
.kepala span{
  font-size: 23px;
}
.kepala .ket{
    position:relative;
    top: -20px;
    padding: 0px;
}
.ket span{
  float: left;
}
.ket .icon{
  font-size: 38px;
  float: right;
  top: -13px;
  position: relative;
  text-align: right;
}
.ket .icon a{
  color: #9F9F9F;
  transition: 0.5s;
}
.ket .icon a:hover{
  color: #1e60a2!important;
}
.ket .icon .satu{
  margin-left: 15px;
}
.abuabu{
  color: #9f9f9f;
}
.icondribbble:hover{
    color: #ea4c89!important;
}
.iconblogger:hover{
    color: #f57d00!important;
}
p{
  margin-bottom: 0px;
}
.s-16{
  font-size: 16px;
}
.s-20{
  font-size: 20px;
}
.s-23{
  font-size: 23px;
}
.s-12{
  font-size: 12px;
}
.s-41{
  font-size: 41px;
}
.semibold{
  font-weight: 600;
}
.bold{
  font-weight: 700;
}
.extrabold{
  font-weight: 800;
}
.line{
  line-height: 2.1em;
}
hr{
  margin: 0px;
}
.subkepala{
  position: absolute;
  bottom: 0;
  width: 100%;
}
.subkepala .container{
  padding: 30px 0px;
}
.subkepala .semibold{
  margin-bottom: 5px;
}
.subkepala .drop{
  color: #1e60a2;
  font-size: 30px;
}
.nomargin{
  margin: 0px;
}

.warnasatu{
  background: #1E60A2;
}
.warnadua{
  background: #F2F2F2;
}
.warnatiga{
  background: #1E60A2;
  opacity: 43%;
}
.textwarnasatu{
  color: #1E60A2
}
section{
  padding: 50px 0px;
  margin: 0px;
}

/*section maintop*/
.Maintop{
    min-height: 100vh;
    /*overflow: hidden;*/
}
/*section satu*/

/*section experience*/
ul.timeline {
    list-style-type: none;
    position: relative;
}
ul.timeline:before {
    content: ' ';
    background: #d4d9df;
    display: inline-block;
    position: absolute;
    left: 29px;
    width: 2px;
    height: 100%;
    z-index: 400;
}
ul.timeline > li {
    margin-bottom: 50px;
    padding-left: 20px;
}
ul.timeline > li:before {
    content: ' ';
    background: #1e60a2;
    display: inline-block;
    position: absolute;
    border-radius: 50%;
    border: 3px solid #1e60a2;
    left: 20px;
    width: 20px;
    height: 20px;
    z-index: 400;
}

/*section awards*/
.garishitam{
  padding: 2px;
  background: #000;
  border-radius: 50px;
  width: 21%;
  margin: 10px 0px;
}

/*section hireme*/
.hireme{
  margin: 0px 10px;
}
.hireme h5{
  float: left;
}
.hireme button{
  float: right;
  transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  transition-duration: 0.3s;
  transition-property: transform;
  border: none!important;
}
.hireme button:hover, .hireme button:focus{
  transform: scale(1.1) rotate(4deg);
  box-shadow: none!important;
}
.hireme .kotak{
  padding: 21px 57px;
  border-radius: 5px;
  box-shadow: 0px 0px 7px #0000006e;
}
.white{
  background: #fff;
}
.white:hover, .white:active, .white:focus{
  background: #f2f2f2!important;
  border-color: none!important;
}

/*section portfolio*/
.btn-outline-seeall{
  border: 2px solid #1E60A2;
  color: #1E60A2;
  top:0px;
  position: relative;
  transition: 0.5s;
  float: right;
}
.btn-outline-seeall:hover, .btn-outline-seeall:focus, .btn-outline-seeall:active{
  border-color: #1E60A2;
  color: #1E60A2;
  top: -10px;
  position: relative;
  box-shadow: none!important;
}


